 * @Email: qinrifeng@163.com
 * @Github: https://github.com/FoneQinrf
 -->
<h3>Icon - 图标</h3>
<hr>
<h4>使用方式</h4>
<pre v-pre><code v-pre class="language-javascript">
<span class="hljs-keyword">import</span> Vue <span class="hljs-keyword">from</span> <span class="hljs-string">'vue'</span>
<span class="hljs-keyword">import</span> { Icon } <span class="hljs-keyword">from</span> <span class="hljs-string">'vvmui'</span>

Vue.component(Icon)

</code></pre>
<h4>所有Icon</h4>
<template>
  <div class="vvm-Icon">
    <div v-for="item in list" :key="item" class="icon" @click="copy" :data-clipboard-text="style(item)">
      <Icon :icon="item" :size="24">
      <div class="name">{{item}}</div>
    </Icon></div>
  </div>
</template>


<h3>API</h3>
<h4>Icon props</h4>
<table>
<thead>
<tr>
<th style="text-align:center">属性</th>
<th style="text-align:left">说明</th>
<th style="text-align:left">类型</th>
<th style="text-align:left">默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:center">icon</td>
<td style="text-align:left">图标对应名称</td>
<td style="text-align:left">String</td>
<td style="text-align:left">无</td>
</tr>
<tr>
<td style="text-align:center">size</td>
<td style="text-align:left">控制图标大小</td>
<td style="text-align:left">Number</td>
<td style="text-align:left">无</td>
</tr>
<tr>
<td style="text-align:center">color</td>
<td style="text-align:left">图标颜色</td>
<td style="text-align:left">String</td>
<td style="text-align:left">无</td>
</tr>
</tbody>
</table>
<h5>更新日志</h5>
<table>
<thead>
<tr>
<th style="text-align:center">版本</th>
<th style="text-align:left">类型</th>
<th style="text-align:left">内容</th>
</tr>
</thead>
<tbody></tbody>
</table>
</section></template>
<style lang="less" scoped>
.vvm-Icon{
  display:flex;
  flex-wrap: wrap;
  .icon{
    padding: 20px;
    cursor: pointer;
  }
}
</style>
<script>
import clipboard from 'clipboard';
export default {
  data(){
    return{
        list:['icongithub','iconxiangzuo','iconloading1','iconloading','iconfile','icon15','iconiconfontmove','iconradioactive','iconzhuangtaijiazai','iconzengjia1','iconshanchu','icondian','iconxiangxia','iconsearch','iconrejected','iconkey','iconaccount','iconlist-rigthl','iconselected','iconzhengque','icontime','iconcuowu','iconcamera','iconuncheck','iconinfo','iconicon-']
    }
  },
  methods:{
    copy(){
      alert('复制成功')
    }
  },
  mounted() {
        new clipboard(".icon");
    },
    computed: {
        style() {
            return function(str) {
                return `<Icon icon="${str}" />`;
            };
        }
    }
};
</script>


